import React from 'react';
import styled from 'styled-components';
import { Avatar, Button, makeStyles } from '@material-ui/core';

import SocialLinks from './UI/SocialLinks';

const useStyles = makeStyles((theme) => ({
  avatar: {
    width: theme.spacing(20),
    height: theme.spacing(20),
    margin: '0 auto',
  },
  button: {
    width: '70%',
    borderRadius: '20px',
    marginBottom: theme.spacing(1),
    marginRight: 'auto',
    marginLeft: 'auto',
  }
}));

const CardWrapper = styled.div`
    text-align: center;
`
const Buttons = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 20px;
`

const Card = (props) => {
    const { person } = props;
    const classes = useStyles();

    return (
        <CardWrapper>
            <Avatar 
                alt='avatar' 
                src={person.avatar} 
                className={classes.avatar} 
            />
            <div>{person.name}</div>
            <div>{person.position}</div>
            <SocialLinks links={person.social_networks} />
            <div>{person.city}</div>
            <Buttons>
                { !person.isContact && <Button variant="outlined" size="small" color="primary" className={classes.button} >Add to contacts</Button>}
                { person.isContact && <Button variant="outlined" size="small" color="secondary" className={classes.button}>Delete from contacts</Button>}
                { !person.isFavourite && <Button variant="outlined" size="small" color="primary" className={classes.button}>Add to favourites</Button>}
                { person.isFavourite && <Button variant="outlined" size="small" color="secondary" className={classes.button}>Delete from favourites</Button>}
            </Buttons>
        </CardWrapper>
    );
};

export default Card;

