import React from 'react';
import { NavLink } from 'react-router-dom';
import styled from 'styled-components';
import { 
    HomeOutlined, 
    ContactsOutlined, 
    FavoriteBorderOutlined, 
    PermIdentityOutlined, 
    ListAltOutlined 
} from '@material-ui/icons';


const NavList = styled.ul`
    margin: 0;
    padding: 0;
    list-style: none;
`;

const NavItem = styled.li`
    color: white;
    text-transform: uppercase;
    text-decoration: none;
`;

const StyledNavLink = styled(NavLink)`
    display: flex;
    align-items: center;
    color: white;
    text-transform: uppercase;
    text-decoration: none;
    box-sizing: border-box;
    padding: 10px 40px;

    &.active {
        background: #39a6f3;
    }

    @media (max-width: 1023px) {
        padding: 10px 20px;
    }
`;

const NavBar = ({ mobile, hideMobileMenu }) => (
    <nav>
        <NavList>
            <NavItem>
                <StyledNavLink  
                    className={ navData => navData.isActive ? 'active' : ''}
                    onClick={ () => {
                        if (mobile) {
                            hideMobileMenu()
                        }
                    }}
                    to='/home'
                >
                    <HomeOutlined color='white' />
                    <div>Home</div>
                </StyledNavLink>
            </NavItem>
            <NavItem>
                <StyledNavLink  
                    className={ navData => navData.isActive ? 'active' : ''}
                    onClick={ () => {
                        if (mobile) {
                            hideMobileMenu()
                        }
                    }}
                    to='/contacts'
                >
                    <ContactsOutlined color='white' />
                    <div>Contacts</div>
                </StyledNavLink>
            </NavItem>
            <NavItem>
                <StyledNavLink  
                    className={ navData => navData.isActive ? 'active' : ''}
                    onClick={ () => {
                        if (mobile) {
                            hideMobileMenu()
                        }
                    }}
                    to='/favourites'
                >
                    <FavoriteBorderOutlined color='white' />
                    <div>Favourites</div>
                </StyledNavLink>
            </NavItem>
            <NavItem >
                <StyledNavLink  
                    className={ navData => navData.isActive ? 'active' : ''}
                    onClick={ () => {
                        if (mobile) {
                            hideMobileMenu()
                        }
                    }}
                    to='/people'
                >
                    <PermIdentityOutlined color='white' />
                    <div>People</div>
                </StyledNavLink>
            </NavItem>
            <NavItem >
                <StyledNavLink
                    className={ navData => navData.isActive ? 'active' : ''}
                    onClick={ () => {
                        if (mobile) {
                            hideMobileMenu()
                        }
                    }}
                    to='/companies'
                >
                    <ListAltOutlined color='white' />
                    <div>Companies</div>
                </StyledNavLink>
            </NavItem>
        </NavList>
    </nav>
);

export default NavBar;